import React from 'react'
import { NavLink } from 'react-router-dom'

export const Navbar = () => {
    return (
        <>
        <div className="container-fluid nav-bg">
            <div className="row">
                <div className=" mx-auto">
                <nav className="navbar navbar-expand-lg navbar-light ">
  <div className="container-fluid ">
    <NavLink className="navbar-brand name" to="/">SouravTech</NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink  className="nav-link" to="/">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="activate" className="nav-link " to="/services">Our Services</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="activate" className="nav-link " to="about">About Us</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="activate" className="nav-link " to="contact">Contact Us</NavLink>
        </li>
       
    
      </ul>
     
    </div>
  </div>
</nav>


                </div>
            </div>
        </div>
        </>
    )
}
